define('util/assert',[],function () {
	"use strict";

	var assert = function (cond, msg) {
		if( !cond ) {
			var error = new Error(msg || "ASSERT failed");

			console.log("Throwing", error.stack);
			console.log();
			
			throw error;
		}
	};

	return assert;
});

